import React, { useState, useEffect } from "react";
import Nav from './Nav'
import Logo from "./Logo";
import Carousel from "./Carousel";
import ThreeCard from "./ThreeCard";
import Top10List from "./Top10List";
import VideoList from "./VideoList";
import Banner from "./Banner";
import CallToAction from "./CallToAction";
import Footer from "./Footer";
import MobileMenu from "./MobileMenu";
import useRWD from "./customHook/useRWD";

import './css/reset.css'
import './css/app.css'

const App = () => {
    const device = useRWD();
    const baseURL = 'https://www.alfred.wiki/api'
    const VideoCategolories = ['ดูหนังออนไลน์ HD', 'พากย์ไทย', 'Action บู๊', 'Comedy ตลก', 'หนังการ์ตูน', 'Fantasy แฟนตาซี', 'Thriller ระทึกขวัญ']


    return (
        <div>
            {device === 'mobile' ? <Logo /> : ''}
            <Nav />
            <Carousel />
            <ThreeCard />
            <Top10List />
            <VideoList
                categolory={VideoCategolories[0]}
                baseURL={baseURL}
            />
            <Banner
                num={2}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[1]}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[2]}
                baseURL={baseURL}
            />
            <Banner
                num={3}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[3]}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[4]}
                baseURL={baseURL}
            />
            <Banner
                num={4}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[5]}
                baseURL={baseURL}
            />
            <VideoList
                categolory={VideoCategolories[6]}
                baseURL={baseURL}
            />
            <CallToAction />
            <Footer />
            <MobileMenu />
        </div>
    )
};

export default App;