import axios from "axios";
import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import "swiper/css/pagination";
import "swiper/css/navigation";
import './css/videoList.css'
import { Navigation } from "swiper";

const VideoList = ({ categolory, baseURL }) => {
    const URL = `${baseURL}/cate/${categolory}/`;
    const [data, setData] = useState([]);

    useEffect(() => {
        const search = async () => {
            if (baseURL) {
                const results = await axios.post(URL);
                setData(results.data.data);
            }
        }
        search();
    }, [])

    const renderedSwiperSlides = data.map(({ alt, src, title, videoTitle }) => {
        return (
            <SwiperSlide key={alt}>
                <img
                    src={src}
                    alt={alt}
                    title={videoTitle}
                />
                <div className="title">
                    {title}
                </div>
            </SwiperSlide>
        );
    });

    return (
        <section className="video-list">
            <div className="container">
                <header className="category">
                    <h2>{categolory}</h2>
                    <a href="#" className="more">ดูทั􀁦งหมด</a>
                </header>
                <Swiper
                    slidesPerView={6}
                    spaceBetween={20}
                    slidesPerGroup={6}
                    grabCursor={true}
                    loopFillGroupWithBlank={true}
                    navigation={true}
                    modules={[Navigation]}
                    breakpoints={{
                        375: {
                            slidesPerView: 3,
                            spaceBetween: 10,
                            slidesPerGroup: 3,
                        },
                        768: {
                            slidesPerView: 3,
                            spaceBetween: 10,
                            slidesPerGroup: 3,
                        },

                        1024: {
                            slidesPerView: 6,
                            spaceBetween: 20,
                            slidesPerGroup: 6,
                        },
                    }}
                    className="mySwiper"
                >
                    {renderedSwiperSlides}
                </Swiper>
            </div>
        </section>
    )
}

export default VideoList;