import axios from "axios";
import React, { useState, useEffect } from "react";
import useRWD from "./customHook/useRWD";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Autoplay } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import './css/carousel.css'

const Carousel = () => {
    const [datas, setDatas] = useState([]);
    const device = useRWD()

    useEffect(() => {
        const search = async () => {
            const results = await axios.post('https://www.alfred.wiki/api/carousel/Index/Index/');
            setDatas(results.data.data);
        };
        search();
    }, []);

    const renderedSwiperSlide = datas.map((data) => {
        const SRC = device === 'PC' ? data.desktop_src : data.mobile_src

        return (
            <SwiperSlide key={data.href}>
                <img src={SRC} alt={data.alt} title={data.title} />
            </SwiperSlide>
        )
    })

    return (
        <section className="carousel">
            <Swiper
                loop={true}
                navigation={true}
                grabCursor={true}
                autoplay={{
                    delay: 2500,
                    disableOnInteraction: false,
                }}
                modules={[Autoplay, Navigation]}
                className="mySwiper"
            >
                {renderedSwiperSlide}
            </Swiper>
        </section>
    )
}

export default Carousel;