import axios from "axios";
import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import './css/three-card.css'


const ThreeCard = () => {
    const [data, setData] = useState([]);

    useEffect(() => {
        const search = async () => {
            const results = await axios.post('https://www.alfred.wiki/api/info/1/');
            setData(results.data.data);
        };
        search()
    }, []);

    const renderedSwiperSlides = data.map(({ adFig, adID }) => {
        return (
            <SwiperSlide key={adID}>
                <div className="pic">
                    <img src={adFig.src} title={adFig.title} alt={adFig.alt} />
                </div>
            </SwiperSlide>
        )
    })

    return (
        <section className="three-card">
            <div className="container">
                <Swiper
                    loop={true}
                    breakpoints={{
                        375: {
                            slidesPerView: 1,
                            spaceBetween: 10,
                        },
                        768: {
                            slidesPerView: 1,
                            spaceBetween: 10,
                        },

                        1024: {
                            slidesPerView: 3,
                            spaceBetween: 10,
                        },
                    }}
                    className="mySwiper">
                    {renderedSwiperSlides}
                </Swiper>
            </div>
        </section>
    )
};

export default ThreeCard;