import axios from 'axios'
import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import 'swiper/css';
import "swiper/css/pagination";
import "swiper/css/navigation";
import './css/top10.css';

const Top10List = () => {
    const [recomms, setRecomms] = useState([]);

    useEffect(() => {
        const search = async () => {
            const results = await axios.post('https://www.alfred.wiki/api/recomm/');
            setRecomms(results.data.data);
        };
        search()
    }, []);


    const renderedSwiperSlides = recomms.map((recomms, index) => {
        return (
            <SwiperSlide key={recomms.href}>
                <div className="item">
                    <img className="num" src={`../material/number/${index + 1}.png`} alt="" />
                    <img src={recomms.src} alt={recomms.alt} title={recomms.title} />
                </div>
            </SwiperSlide>
        )
    })


    return (
        <section className="top-ten video-list">
            <div className="container">
                <h2>Top 10 in Thailand</h2>
                <Swiper
                    slidesPerView={6}
                    spaceBetween={20}
                    slidesPerGroup={6}
                    grabCursor={true}
                    loopFillGroupWithBlank={true}
                    navigation={true}
                    modules={[Navigation]}
                    breakpoints={{
                        375: {
                            slidesPerView: 2,
                            spaceBetween: 10,
                            slidesPerGroup: 2,
                        },
                        768: {
                            slidesPerView: 2,
                            spaceBetween: 5,
                            slidesPerGroup: 2,
                        },

                        1024: {
                            slidesPerView: 4.5,
                            spaceBetween: 20,
                            slidesPerGroup: 4,
                        },
                    }}
                    className="mySwiper"
                >
                    {renderedSwiperSlides}
                </Swiper>

            </div>
        </section>
    )
}

export default Top10List;