import React, { useState, useEffect } from "react";
import './css/nav.css';

const Nav = () => {
    const [isShow, setIsShow] = useState(false)
    const style = {
        outline: '1px solid white',
    }

    return (
        <nav className="nav">
            <div className="container">
                <div className="wrapper">
                    <div className="logo">
                        <img src="./material/sawadika_500_250.png" alt="" />
                    </div>
                    <ul>
                        <li><a href="#">หนังฝรัง􀁣</a></li>
                        <li><a href="#">หนังไทย</a></li>
                        <li><a href="#">หนังเกาหลี</a></li>
                        <li><a href="#">หนังญ􀁣ีปุ􀁤น</a></li>
                        <li><a href="#">หนังจีน</a></li>
                    </ul>
                    <div className="logo">
                        <img src="./material/sawadika_300_80.png" alt="" />
                    </div>
                    <div
                        className="search-bar"
                        style={isShow ? style : {}}
                    >
                        <i
                            className="search fa-solid fa-magnifying-glass"
                            onClick={() => setIsShow(!isShow)}
                        />
                        {isShow ? <input type="text" placeholder="search video" /> : ''}

                    </div>
                    <a href="#" className="red-btn btn btn">สมัคร</a>
                </div>
            </div>
        </nav >
    )
};

export default Nav;